import React from 'react';
import styled from "styled-components";

const StyledSkills = styled.div`
  position: relative;
  overflow: hidden;
  display: inline-flex;
  width: calc(50% - 2em);
  margin: 1em;
  padding: 3em 2em 1em;
  border-radius: 2em;
  ${props => props.type === "Tech Skills" && `
  background: linear-gradient(45deg,#f2eaff,#e7f5ff);
  `};
  ${props => props.type === "Soft Skills" && `
  background: linear-gradient(45deg,#d6f7ff,#eaffecc4);
  `};
  @media(max-width: 900px) {
  width: 100%;
}
`
const SkillsTitle = styled.div`
  position: absolute;
  top: -0.45em;
  right: 0;
  font: 600 5em Oswald;
  color: #bbbbbb26;
  z-index: 0;
  @media(max-width: 900px) {
    font-size: 3em;
  }
`
const StyledSkillItem = styled.div`
  background: #fff;
  font-weight: 600;
  padding: 0.5em 1em;
  margin:0.3em;
  border-radius: 0.8em;
  z-index: 1;
`

const Skills = (props) => {
    return (
        <StyledSkills {...props}>
            <SkillsTitle>{props.type}</SkillsTitle>
            {props.skills.map(skill => {
                return (
                    <StyledSkillItem>{skill}</StyledSkillItem>
                )

            })}
        </StyledSkills>
    );
};

export default Skills;