import React from 'react';
import styled from "styled-components";

const StyledCourse = styled.div`
  background: #fff;
  font-weight: 500;
  padding: 0.7em 2em;
  border-radius: 0.8em;
  line-height: 1.4em;
  z-index: 1;
`

const Course = (props) => {
    return (
        <StyledCourse>
            {props.name}
        </StyledCourse>
    );
};

export default Course;