import React from 'react';
import styled from "styled-components";

const StyledList = styled.div`
  position: relative;
  overflow: hidden;
  display: inline-flex;
  width: 100%;
  margin: 1em;
  padding: 5em 2em 2em;
  border-radius: 2em;
  background: ${props => props.background || '#ccc'};
  ${props => props.half && `
  width: calc(50% - 2em);
  padding: 3em 2em 1em;
  > *:first-child {
  font-size: 5em;
  }
    @media(max-width:900px) {
     width: 100%;
    }
    > *:first-child {
  font-size: 3em;
  }
  }
  `};
  @media(max-width:900px) {
      padding: 3em 1em 1em;
    }
  
`
const Title = styled.div`
  ${props => props.half && `
    font-size: 5em;
   `}
  position: absolute;
  top: -0.45em;
  right: 0;
  font-size: 8em;
  font-weight: 600;
  font-family: Oswald, sans-serif;
  color: #bbbbbb26;
  z-index: 0;
  @media(max-width: 900px) {
    font-size: 3.5em;
  }
`
const StyledItems = styled.div`
  background: #FFF;
  border-radius: 1.5em;
  width: 100%;
  padding: 0.5em 0;
  z-index: 1;
`


const List = (props) => {
    return (
        <StyledList {...props}>
            <Title>{props.title}</Title>
            <StyledItems>
                {props.children}
            </StyledItems>
        </StyledList>
    );
};

export default List;