import React from 'react';
import styled from "styled-components";

const StyledProject = styled.div`
    width: 100%;
  padding: 1.5em 2em;
  z-index: 1;
`
const Name = styled.div`
    width: 100%;
  font-weight: 700;
  font-size:1.3em;
margin-bottom:0.3em;
`
const Description = styled.div`
  margin-bottom:0.4em;
`
const Link = styled.a`
color: var(--acsent);
`
const Activities = styled.ul`
  padding: 1em 0 0 1em;
  list-style-type: square;
  > li {
    line-height: 1.5em;
  }
`

const Project = (props) => {
    return (
        <StyledProject {...props}>
            <Name>{props.name}</Name>
            <Description>{props.description}</Description>
            <Link href={props.link} target="_blank">{props.link}</Link>
            <Activities>
                {props.children}
            </Activities>
        </StyledProject>
    );
};

export default Project;