import React from 'react';
import styled from "styled-components";

const StyledWorkItem = styled.div`
    width: 100%;
  padding: 1.5em 2em;
  z-index: 1;
  @media(max-width:900px) {
    padding: 1.5em 1.5em;
  }
`
const Position = styled.div`
    width: 100%;
font-size: 1.5em;
  font-weight: 700;
  margin-bottom:0.2em;
`
const Where = styled.div`
    color: var(--acsent);
  font-size: 1.1em;
  font-weight: 500;
  margin-bottom:0.2em;
`
const Period = styled.span`
  opacity: 0.7;
`
const Activities = styled.ul`
  padding: 1em 0 0 1em;
  list-style-type: square;
  > li {
    line-height: 1.5em;
  }
`
const dateToString = (date) => {
    return (
        date.toLocaleDateString("en-US", {year: 'numeric', month: 'short' })
    )
}

let today = new Date()
const period = (from, till={today}) => {
    let diff = new Date(till.getTime() - from.getTime())
    let year = diff.getUTCFullYear() - 1970
    let month = diff.getUTCMonth()
    return year + " y. " + month + " m."

}
const tilldate = (till) => {
    if (till === today) {
        return "Present"
    } else {
        return dateToString(till)
    }
}
const WorkItem = ({position, where, from, till=today, country, children}) => {

    return (
        <StyledWorkItem>
            <Position>{position}</Position>
            <Where>{where}</Where>
            <Period>{dateToString(from)} ─ {tilldate(till)}  ◦  {period(from, till )} | {country}</Period>
            <Activities>
            {children}
            </Activities>
        </StyledWorkItem>
    );
};

export default WorkItem;