import React from 'react';
import styled from "styled-components";

const Degree = styled.span`
    font-size: 1.3em;
  font-weight: 700;
  margin-bottom:0.2em;
  display: inline-flex;
`
const Institution = styled.span`
  margin-bottom: 0.2em;
  display: inline-flex;
`
const Ocupation = styled.span`
    
`
const Where = styled.span`
`
const Years = styled.div`
  opacity: 0.7;
`
const StyledEducationItem = styled.div`
    width: 100%;
  padding: 1.5em 2em;
  z-index: 1;
  @media(max-width:900px) {
    padding: 1.5em 1.5em;
  }
`

const EducationItem = (props) => {
    return (
        <StyledEducationItem {...props}>
            <Degree>{props.degree}</Degree>,&nbsp;<Ocupation>{props.ocupation}</Ocupation>
            <div></div>
            <Institution>{props.institution}</Institution>&nbsp;|&nbsp;<Where>{props.where}</Where>
            <Years>{props.years}</Years>
        </StyledEducationItem>
    );
};

export default EducationItem;