import React from 'react';
import styled from "styled-components";

const Avatar = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  height: 18em;
  width: 18em;
  
  background-size: cover;
  border-radius: 35%;
  z-index: 1;
  @media(max-width: 900px) {
    position: relative;
    margin-bottom:22em;
  }
`
const About = styled.div`
  position: relative;
  margin: 4em 0 0 11em;
  padding: 2em 2em 2em 10em;
  align-content: flex-end;
  background: #fff;
  border-radius: 2em;
  z-index: 0;
  >* {
    width: 100%;
    line-height: 1.4;
  }
  h1 {
    line-height: 1;
    margin: 0.4em 0;
  }
  @media(max-width: 900px) {
    position: absolute;
    margin: 0;
    padding: 4em 2em 2em;
    width: calc(100% + 2em);
    
    bottom: 0;
    > * {
      text-align: center;
    }
  }
`

const Head = (props) => {
    return (
        <>
            <Avatar src={props.avatar}/>
            <About>
                <h1 className="fullname">{props.fullname}</h1>
                <span className="specialization">{props.spec}</span>
                <div className="shortwelcome">
                    {props.children}
                </div>
            </About>

        </>
    );
};

export default Head;