import React from 'react';
import styled from "styled-components";
import Phone from '@material-ui/icons/Phone';
import {Email, LinkedIn, Room} from '@material-ui/icons';

const BlueBack = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0.3em 2em;
  background: linear-gradient(45deg, #2a9fc3, #2ac3b5);
  border-radius: 1em;
`
const CItem = styled.a`
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 25%;
  padding: 0.3em 0.6em;
  color: #fff;
  pointer-events: none;

  svg {
    margin-right: 0.5em;
  }

  ${props => props.link && `
  pointer-events: auto;
  `};
  @media(max-width: 900px) {
    font-size: 0.8em;
    width: 50%;
  }
`


const ContactInfo = (props) => {
    return (
        <BlueBack>
            <CItem><Email/>{props.email}</CItem>
            <CItem><Room/>{props.location}</CItem>
            <CItem><Phone/>{props.tellnumber}</CItem>
            <CItem link href={props.linkidin} target="_blank"><LinkedIn/>{props.linkidin}</CItem>
        </BlueBack>
    );
};

export default ContactInfo;