import React from 'react';
import styled from "styled-components";

const StyledDivider = styled.div`
width: ${props => props.wt || '100%'};
  height: ${props => props.ht || '100%'};
  ${props => props.line && `
  height: 1px;
  width: calc(100% - 4em);
  margin: 0 2em;
  background: #ccc;
  display: block;
  `}
`

const Divider = (props) => {
    return (
        <StyledDivider {...props} />
    );
};

export default Divider;